import { _decorator, Component, Node, Prefab, instantiate, log, Gradient, Graphics, EventTouch, v3, Color } from 'cc';
import { level_progress_bar } from '../../transiton/level_progress_bar';
const { ccclass, property } = _decorator;

@ccclass('level4_science')
export class level4_science extends Component {
    @property(level_progress_bar)
    levelBarUI: level_progress_bar = null;
    @property(Node)
    level: Node = null;
    @property(Prefab)
    gPrefab: Prefab = null;
    @property(Node)
    area: Node = null;

    private graphics = null;
    @property(Node)
    private draw: Node = null;
    start() {
        this.initGraphic();
        this.draw.on(Node.EventType.TOUCH_START, this.onTouchStart.bind(this));
        this.draw.on(Node.EventType.TOUCH_MOVE, this.onTouchMove.bind(this));
        this.draw.on(Node.EventType.TOUCH_END, this.onTouchEnd.bind(this));
    }
    initGraphic(){
        // this.draw = instantiate(this.gPrefab);
        // this.area.addChild(this.draw);
        this.graphics = this.draw.getComponent(Graphics);
        this.graphics.lineWidth=10;
        this.graphics.strokeColor = Color.BLACK;
    }
    onTouchStart(event: EventTouch){
        let p = event.getUILocation();
        let out = v3();
        this.area.inverseTransformPoint(out, v3(p.x, p.y, 0));
        let g = this.graphics;
        g.moveTo(out.x, out.y);
        g.stroke();
    }
    onTouchMove(event: EventTouch){
        let p = event.getUILocation();
        log("check p: ", p);
        let out = v3();
        this.area.inverseTransformPoint(out, v3(p.x, p.y, 0));
        let g = this.graphics;
        g.lineTo(out.x, out.y);
        g.stroke();
    }
    onTouchEnd(){

    }

    update(deltaTime: number) {
        
    }
}


